.header {
    height: 120px ;
    display:  flex ;
    align-items: center ; 
    background-color: #131921  ; 
    position: sticky   ; 
     top:0 ; 
     z-index:100 ; 

}
.header__logo {
    width: 80px  ; 
    object-fit: contain ; 
    margin: 0 20px  ; 
    margin-top: 1px ; 

}
.header__search {
    display: flex  ; 
     flex :1  ; 
      align-items: center  ; 
      border-radius:24px ; 
}
.header__search{
    display: flex ;
    flex: 1; 
    align-items: center ;  
    border-radius: 24px ; 

}

.header__searchInput{
    height: 12px ;
    padding: 10px  ; 
    border: none ; 
    width:100% ; 

}
.header__searchIcon {
   padding: 6.5px  ; 

   height: 20px !important;
   background-color: firebrick;
}
.header__optionLineOne{
    font-size:10px ; 
margin-left: 10px ;

}
.header__optionLineTwo {
    font-size:13px ; 
    font-weight :800;
    margin-left: 10px ;


}
.header__optionBasket {
    display: flex;
    align-items: center ; 
    color: white ; 
    margin-right: 20px;
}
.header__basketCount{
    margin-left: 10 px ;
    margin-right: 10 px;
}
.header__nav{
    display: flex;
    justify-content: space-evenly;
}
.header__option {
    display: flex ; 
    flex-direction: column ; 
    margin-left: px  ; 
    margin-right :10px ; 
    color:floralwhite ; 

}