}
.checkoutProduct__info{
    padding-left: 20px ;
}
.checkoutProduct__info > button{
    background: #62d404 ; 
     border: 1px solid   ; 
     margin-top: 10px      ;
    border-color: #3462a8 #9c7e31 #846a29 ; 
    color: #111  ; 

    }
